<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/dictionaryType/data" type='list' :params='callBackParams'>
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse='false'>
        <li>设置为系统字典，表示该字典重要程度较高</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
       <el-form-item label="字典编码" prop="dictCode" :error='errorMessage.dictCode'>
          <el-input v-model="formData.dictCode" :autofocus="true" ref="first"></el-input>
        </el-form-item>
        <el-form-item label="字典标签" prop="dictLabel" :error='errorMessage.dictLabel'>
          <el-input v-model="formData.dictLabel" :autofocus="true" ref="first"></el-input>
        </el-form-item>
        <el-form-item label="是否系统内置" prop="isSystem" :error='errorMessage.isSystem'>
          <cms-switch v-model="formData.isSystem"></cms-switch>
        </el-form-item>
        <el-form-item label="备注信息" prop="remark" :error='errorMessage.remark'>
          <el-input v-model="formData.remark" type="textarea" :autosize="{ minRows: 3}"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="update" v-color >保存修改</cms-button>
        <cms-button type='rest' @click="reset">重置</cms-button>
        <!-- <cms-button type='rest' @click="test">mock测试</cms-button> -->
      </div>
    </div>
  </div>
</template>

<script>
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import { dictDataManagerGet, dictDataManagerUpdate, checkCode } from './../../../../../api/setting/dictionary.js'
var defaultData = {
  dictLabel: '',
  isSystem: true,
  sortNum: 10,
  dictTypeId: '',
  dictCode: '',
  dictType: '',
  remark: ''
}
export default {
  name: 'dictionaryDataEdit',
  mixins: [form],
  data() {
    var isCheckCode = (rule, value, callback) => { // 验证类型是否重复
      checkCode({
        id: this.formData.id,
        dictCode: value,
        dictTypeId: this.$route.query.dictTypeId }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('字典编码已存在'))
        }
      })
    }
    return {
      dictTypeId: this.$route.query.dictTypeId,
      dictType: this.$route.query.dictType,
      formData: Object.assign({}, defaultData),
      rules: {
        dictCode: [rules.required('字典编码必填'), rules.len(1, 20), rules.english(), rules.custom(isCheckCode)],
        dictTypeId: [rules.required('此项必填')],
        dictLabel: [rules.required('请填写字典标签名称'), rules.len(1, 20)],
        isSystem: [rules.required()],
        remark: [rules.max(140, '最大长度不能超过140个字符')]
      }
    }
  },
  computed: {
    callBackParams() {
      return {
        dictTypeId: this.$route.query.dictTypeId,
        dictType: this.$route.query.dictType
      }
    }
  },
  methods: {
    getFormData(id) {
      // 通过id获取表单信息
      dictDataManagerGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
      })
    },
    update() {
      this.updateForm(dictDataManagerUpdate, this.formData, '/dictionaryType/data', 'list', this.callBackParams)
    },
    reset() {
      this.getFormData(this.$route.query.id)
      this.$refs.form.resetFields()
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>

<style>
</style>



